.aboutBack {
  background: url("../images/second.jpg") no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.2);
}

.about__me {
  font-family: Iskry regular;
  color: white;
  font-size: 80px;
}

.about__container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
}

.about__me-text {
  width: 700px;
  color: white;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.2em;
  font-weight: 100;
  margin-bottom: 20px;
  font-family: Helvetica;
}

.about__image__flexible {
  display: flex;
  align-items: flex-end;
  box-shadow: inset 0 0 0 2000px #dedcd1;
  opacity: 0.5;
}

.about__image {
  width: 400px;
  opacity: 0.8;
}

.about__text__field {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  width: 400px;
}

.about__first-game {
  color: rgb(255, 255, 255);
  height: 50px;
  font-size: 35px;
  font-family: "Iskry Regular", serif;
}

.about__description {
  color: rgb(255, 255, 255);
  height: 200px;
  width: 175px;
  font-size: 20px;
  line-height: 150%;
  font-style: Helvetica;
}

.about__row__align__main-flex {
  display: flex;
}

.columnTest {
  display: flex;
  flex-direction: column;
}

.about__fix-broken__image {
  max-width: 200px;
  max-height: 600px;
  margin-left: 40px;
  margin-right: 25px;
  border-radius: 40px;
  object-fit: cover;
}

.about__image__fix {
  max-width: 200px;
  max-height: 500px;
  margin-left: 50px;
  margin-right: 25px;
  border-radius: 40px;
  object-fit: cover;
}

.about__fix {
  width: 20px;
  margin-bottom: 30px;
}

@media only screen and (max-width: 766px) {
  .about__row__align__main-flex {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 15vh;
  }
  .about__image__fix {
    float: left;
    width:  20vh;
    height: 35vh;
    object-fit: cover;
    margin-left: 10px;
    margin-right: 25px;
    border-radius: 40px;
    object-fit: cover;
  }
  .about__me {
    font-size: 60px;
  }
  .about__description {
    font-size: 16px;
  }
  .aboutBack {
    height: 230vh;
    border:0!important;
  }

}

@media only screen and (min-width: 438px) {
  @media only screen and (max-width: 479px) {
    .about__row__align__main-flex {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
}
